:root {
  --white: #fff;
  --darkgrey: #56534c;
  --darkred: #a47d5e;
  --grey:#56534c; 
  --bigred: #dfc691;
  --lightgrey: #979285;
  --aleg: 'Alegreya', serif;
  --cedar: 'Cedarville Cursive', cursive;
  --metro: 'Metrophobic', sans-serif;
  --play: 'Playfair Display', serif;
  --sectionTitle: 28px;
  --subTitle: 12px;
  --paraText: 12px;

}

*{
  padding:0;
  margin:0;
  font-family: var(--aleg);
}

h3{
  width: 100%;
  text-align: center;
  text-transform: capitalize;
  color: var(--white);
  font-size:42px;
}

.Wrapper{
  width:100%;
  max-width: 1200px;
  margin:auto;
}
/*
font-family: 'Alegreya', serif;
font-family: 'Cedarville Cursive', cursive;
font-family: 'Metrophobic', sans-serif;
font-family: 'Playfair Display', serif;
*/

.fixed-menu{
  background-color: rgba(0,0,0,0.5);
  font-family: var(--aleg);
  display:flex;
  justify-content: space-around;
  align-items:center;
  position:fixed;
  margin:0;
  width:100%;
  max-width: 1200px;
}

.logo{
  flex:1;
  padding-top: 5px;
  text-align: center;
}


.main-menu{
  flex:3;
  font-size:14px;
  text-align: center;
}

.main-menu ul  {
  display:flex;
  flex-direction: row;
  justify-content:space-around;
  padding:0;
  margin:0;
  list-style: none;

}

.main-menu ul, .main-menu ul li{
  padding:0; 
  margin:0;
  list-style: none;
  
}

.main-menu a, .main-menu li{
  color: #fff;
  text-decoration: none;
}

.main-menu a.nav-active {
  color: rgb(0, 0, 0);
  /*text-shadow: 1px 1px 1 #fff;*/
  background-color: #fff;
  padding:5px;
}

.main-menu a:hover{
  color: rgb(0, 0, 0);
}

.Social-Media{  
  flex:1;
  text-align: right;
  margin-right: 45px
}
.Social-Media a{
  color:#fff;
  padding-right:15px;
  padding-left: 10px;  
}

.Social-Media a:hover{
  color:#000;
}

/*welcome Page */
.welcome-page{
  float:left;
  width: 100%;
 
}

.welcome-page >  div{
  width: 100%;
}

.banner{
  display:grid;
  grid-template-columns: repeat (4,1fr);
  grid-template-rows:repeat (5, 200px);
  color: #fff;
}

.banner-img{
  grid-column: 1/-1;
  grid-row: 1/10;
}
.banner-img>img{
  width: 100%;
}

.banner-heading{
  grid-column: 1/-1;
  grid-row: 3/ 4;
  text-align: center;
  font-family: var(--play);
  font-size: 70px;
  font-weight:bold;
  
}

.banner-dot-divider{
  grid-column: 1/-1;
  grid-row: 4/ 5;
  text-align: center;
}

.banner-quotes{
  grid-column: 1/-1;
  grid-row: 5/ 6;
  text-align: center;
  font-family: var(--metro);
  font-size: 20px;
  padding-top: 20px;
}

.banner-button{
  grid-column: 1/-1;
  grid-row: 6/7;
  text-align: center;
  font-family: var(--aleg);
  padding-top: 100px;

}

.banner-button > a > button{
  color: #fff;
  padding: 15px 20px;
  background-color: rgba(116, 116, 116, 0.5);
  border: 1px dashed #fff;
  cursor: pointer;
}

.banner-button > a > button:hover{
  color: #000;
  background-color: #fff;
  border: 1px dashed #fff;
}

.art-of{
  width:100%;
}

.art-of-left, .art-of-rightt{
  width: 50%;
  float:left;
}
.art-of-left > h3{
  color: var(--darkgrey);
  font-size: var(--sectionTitle);
}
.art-of-left > h5{
  width: 100%;
  text-align: center;
  text-transform:uppercase;
  font-size: var(--subTitle);
  color: var(--darkred);
  padding-bottom: 50px;
}
.art-of-left >  p{
  width:80%;
  padding: 0%  10%;
  text-align: center;
  font-size: var(--paraText);
}

.featured{
  width:100%;
  text-align:  center;
}
.featured > h2{
  color: var(--darkgrey);
  font-size: 18px;
  width: 100%;
  text-align: center;
  padding:25px 0;
}
.featured > div, .featured > div.quotes{
  display:flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}
.featured > div > p{
  border-top:  1px solid #56534c;
  border-bottom: 1px solid #56534c;
  padding: 10px  10px;
  margin-top: 10px;
}
.featured > div > img{
  width: 150px;
  height:auto;
  border-radius: 90%;
}
.featured > div.quotes > p{
  border: none;
  font-family: var(--cedar);

}


.art-of-right {
  display:grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
}

.main-featured{
  width:80%;
  padding: 10% 10%;
  background-image: url(./img/BG_featured.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size:auto;

}



.breakfast{
  width:100%;
  background-image: url(./img/BG_Breakfast.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size:auto;
  float:left;
}
.breakfast-left {
  float:left;
  width: 50%;
}
.breakfast-left > h3  {
  float:left;
  padding-top: 10%;
}

.breakfast-left > h5  {
  float:left;
  width: 100%;
  text-align: center;
  text-transform:uppercase;
  color: #c8a593;
  font-size:16px;
  text-shadow: 1px 1px rgb(116, 116, 116);
  padding-bottom: 10%;
}
.breakfast-left >p  {
  float:left;
  text-align: center;
  padding-left: 50px;
  color: #fff;
  padding-bottom:  30px;
}

.menu-name{
  font-size: 24px;
  font-weight:  bold;
  padding-top: 20px;
}

.menu-price{
  font-size: 30px;
  color: #c8a593;
  font-weight: bold;
}

.thumbnail-slider-wrap {
  margin-top: 15px;
  height: 85px;
}
.thumbnail-slider-wrap .slick-track .slick-slide {
  text-align: center;
}
.thumbnail-slider-wrap .slick-track .slick-slide img {
  width: 70%;
}

.slideleft{
  float:left;
  width: 30%;
}
.slideleft img{
  width: 100%;
}